.task-create {
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
}

.back-button,
.submit-button {
    margin: 10px 0;
    padding: 10px 15px;
    background-color: #fdd835;
    color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

.back-button:hover,
.submit-button:hover {
    background-color: #e6c300;
}

.drop-zone {
    margin: 20px 0;
    padding: 20px;
    border: 2px dashed #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
    cursor: pointer;
}

.drop-zone p {
    margin: 0;
    color: #666;
}

.description-input {
    width: 100%;
    height: 100px;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    resize: none;
    font-size: 16px;
}
.name-input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
}

/* General mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 90%;
        padding: 10px;
    }
    table {
        width: 100%;
        font-size: 0.9em;
        overflow-x: auto;
        display: block;
    }
    .button, button {
        font-size: 14px;
        padding: 8px;
    }
    input, select, textarea {
        width: 100%;
        font-size: 1em;
    }
    .flex-container {
        flex-direction: column;
    }
    .card {
        margin-bottom: 15px;
    }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
        font-size: 95%;
    }
    table {
        font-size: 1em;
    }
    .button, button {
        font-size: 16px;
    }
}
