.account-list {
    padding: 20px;
}

h2 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
}

.add-account {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    gap: 10px;
}

.add-account input,
.add-account select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    width: 200px;
}

.add-account button {
    padding: 10px 20px;
    background-color: #8b9a5b;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.add-account button:hover {
    background-color: #6f7e46;
}

.accounts-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.accounts-table th,
.accounts-table td {
    padding: 10px;
    border: 1px solid #ddd;
    text-align: left;
}

.accounts-table th {
    background-color: #fdd835;
    color: #333;
    font-weight: bold;
}

.accounts-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.accounts-table tr:hover {
    background-color: #f1f1f1;
}

.accounts-table button {
    padding: 5px 10px;
    background-color: #f44336; /* Червоний для видалення */
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 14px;
}

.accounts-table button:hover {
    background-color: #d32f2f;
}

/* General mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 90%;
        padding: 10px;
    }
    table {
        width: 100%;
        font-size: 0.9em;
        overflow-x: auto;
        display: block;
    }
    .button, button {
        font-size: 14px;
        padding: 8px;
    }
    input, select, textarea {
        width: 100%;
        font-size: 1em;
    }
    .flex-container {
        flex-direction: column;
    }
    .card {
        margin-bottom: 15px;
    }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
        font-size: 95%;
    }
    table {
        font-size: 1em;
    }
    .button, button {
        font-size: 16px;
    }
}
