.task-view {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
}

.back-button {
    margin-bottom: 20px;
    padding: 10px 15px;
    background-color: #fdd835;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

.back-button:hover {
    background-color: #e6c300;
}

.task-container {
    margin: 20px 0;
}

.media-container video,
.media-container audio {
    max-width: 100%;
    height: auto;
}

.description h3 {
    margin-top: 10px;
    font-size: 20px;
    font-weight: bold;
}

.answer-input {
    width: 100%;
    height: 100px;
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
    resize: none;
}

.task-navigation {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 10px;
}

.previous-button,
.next-button,
.submit-button {
    padding: 10px 15px;
    background-color: #fdd835;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

.previous-button:hover,
.next-button:hover,
.submit-button:hover {
    background-color: #e6c300;
}

/* General mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 90%;
        padding: 10px;
    }
    table {
        width: 100%;
        font-size: 0.9em;
        overflow-x: auto;
        display: block;
    }
    .button, button {
        font-size: 14px;
        padding: 8px;
    }
    input, select, textarea {
        width: 100%;
        font-size: 1em;
    }
    .flex-container {
        flex-direction: column;
    }
    .card {
        margin-bottom: 15px;
    }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
        font-size: 95%;
    }
    table {
        font-size: 1em;
    }
    .button, button {
        font-size: 16px;
    }
}
