.assignments {
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
}

.assignment-controls {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.assignment-controls select,
.assignment-controls button {
    padding: 10px;
    font-size: 16px;
}

.assignment-controls button {
    background-color: #fdd835;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.assignment-controls button:hover {
    background-color: #e6c300;
}

/* General mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 90%;
        padding: 10px;
    }
    table {
        width: 100%;
        font-size: 0.9em;
        overflow-x: auto;
        display: block;
    }
    .button, button {
        font-size: 14px;
        padding: 8px;
    }
    input, select, textarea {
        width: 100%;
        font-size: 1em;
    }
    .flex-container {
        flex-direction: column;
    }
    .card {
        margin-bottom: 15px;
    }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
        font-size: 95%;
    }
    table {
        font-size: 1em;
    }
    .button, button {
        font-size: 16px;
    }
}
