.student-submissions {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
}

.submissions-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.submissions-table th,
.submissions-table td {
    padding: 10px;
    border: 1px solid #ddd;
}

.submissions-table th {
    background-color: #8b9a5b; /* Хакі */
    color: #fff;
}

.submissions-table tr:hover {
    background-color: #f5f5f5; /* Світло-сірий */
}

.back-button {
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: #8b9a5b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
    font-size: 16px;
}

.back-button:hover {
    background-color: #6d7d4f;
}
