.statistics {
    padding: 20px;
}

.group-selector {
    margin-bottom: 20px;
}

.group-selector label {
    margin-right: 10px;
    font-weight: bold;
}

#group-select {
    padding: 5px;
    font-size: 16px;
}

/* General mobile styles */
@media (max-width: 768px) {
    body {
        font-size: 90%;
        padding: 10px;
    }
    table {
        width: 100%;
        font-size: 0.9em;
        overflow-x: auto;
        display: block;
    }
    .button, button {
        font-size: 14px;
        padding: 8px;
    }
    input, select, textarea {
        width: 100%;
        font-size: 1em;
    }
    .flex-container {
        flex-direction: column;
    }
    .card {
        margin-bottom: 15px;
    }
}

/* Tablet styles */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
        font-size: 95%;
    }
    table {
        font-size: 1em;
    }
    .button, button {
        font-size: 16px;
    }
}
